<template>
  <div class="container">
    <div class="row">
      <div class="col-2"></div>
      <div class="col-7">
        <div class="mb-3">
          <label class="form-label">Nickname</label>
          <input
            type="text"
            class="form-control"
            v-model="userS.nickname"
          />
          <button class="btn btn-primary" @click="addNickname">Dat nickname</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getAuth, updateProfile } from "firebase/auth";
import store from '../store/index'

export default {
  data: () => {
    return {
      userS: {
        nickname: "",
      },
    };
  },
  methods: {
    addNickname() {
      const auth = getAuth();
      updateProfile(auth.currentUser, {
        displayName: this.userS.nickname,
      })
        .then(() => {
          store.commit("SET_NAME", this.userS.nickname);
        })
        .catch((error) => {
          // An error occurred
          // ...
        });
    },
  },
};
</script>

